.family {
    position: relative;
    z-index: 1;
    text-align: center;

    .background-text {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateY(-50%) translateX(-50%);
    }

    &__logos {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        @include media-breakpoint-down(md) {
            display: none;
        }
    }

    &__logo {
        position: absolute;

        &:nth-child(1) {
            top: 0;
            left: 0;
        }

        &:nth-child(2) {
            top: 15%;
            left: 32%;
        }

        &:nth-child(3) {
            top: 10%;
            left: 60%;
        }

        &:nth-child(4) {
            top: 5%;
            right: -10%;
        }

        &:nth-child(5) {
            top: 72%;
            left: -14%;
            transform: translateY(-50%);
        }

        &:nth-child(6) {
            bottom: 0;
            left: 12%;

        }

        &:nth-child(7) {
            bottom: 0;
            left: 57%;
        }

        &:nth-child(8) {
            bottom: 14%;
            right: -15%;
            transform: translateY(-50%);
        }
    }

    &__wrapper {
        position: relative;
        padding: 150px 0;

        @include media-breakpoint-down(md) {
            padding: 0;
        }
    }
}
